* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #2d2d37;
}

.container {
  width: 80%;
  display: flex;
  justify-content: space-around;
}

@media (max-width: 930px) {
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}